  getTags() {
    Axios.get(this.baseService.baseUrl + 'secure/tags/allTags',{
      headers: {
        'x-auth' : this.baseService.authToken

      },
      
    })
      .then(response => {        
       
        if (response.data.status) {
          this.tagNames = response.data.data;
          let tmp = [];
            for(let i=0; i < this.tagNames .length; i++) {
              tmp.push({ id: this.tagNames[i]._id, itemName: this.tagNames[i].tag_name });
            }
            this.dropdownList = tmp;
          console.log("DropDown Data");
          console.log(this.dropdownList);
        }
        else if(this.baseService.checkToken(response.data.message , response.data.status))
        {

        }
        else
        {
        }
        
      })
      .catch(function (error) {
        console.log(error)
      })
  }

