import { NgModule,CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { BrowserModule } from '@angular/platform-browser';
import { NgbDate,NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { TicketsComponent } from './tickets/tickets.component';
import {QuillModule} from 'ngx-quill'
// import {SharedModule} from '@gaxon/modules';
import {CKEditorModule} from 'ngx-ckeditor';
import { SidebarComponent } from './sidebar/sidebar.component';
import { AngularMultiSelectModule } from 'angular2-multiselect-dropdown';
import { TicketListComponent } from './ticket-list/ticket-list.component';
import { InprogressComponent } from './inprogress/inprogress.component';
import { ResolvedListComponent } from './resolved-list/resolved-list.component';
import { ReopenedListComponent } from './reopened-list/reopened-list.component';
import { EditTicketComponent } from './edit-ticket/edit-ticket.component';
import { CommentComponent } from './comment/comment.component';
import { DataTablesModule } from 'angular-datatables';
import { NgxPaginationModule } from 'ngx-pagination';
import {NgxMasonryModule} from 'ngx-masonry';
import { SharedModule } from '@gaxon/modules';
import { AssignTicketsComponent } from './assign-tickets/assign-tickets.component';
// import { NgbdDemoModule } from '@app/content/com';

const appsRoutes: Routes = [
  {
    path: 'ticket-generate',
    component: TicketsComponent
  },
  {
    path: 'ticket-list',
    component: TicketListComponent
  },
  {
    path: 'ticket-inprogress',
    component: InprogressComponent
  },
  {
    path: 'ticket-resolved',
    component: ResolvedListComponent
  },
  {
    path: 'ticket-reopend',
    component: ReopenedListComponent
  },
  {
    path: 'ticket-edit',
    component: EditTicketComponent
  },
  {
    path: 'assign-tickets',
    component: AssignTicketsComponent
  },
  {
    path: 'comments',
    component: CommentComponent
  },
];



@NgModule({
  declarations: [TicketsComponent, SidebarComponent, TicketListComponent, InprogressComponent, ResolvedListComponent, ReopenedListComponent, EditTicketComponent, CommentComponent, AssignTicketsComponent],
  imports: [
    CommonModule,
    CKEditorModule,
    NgxMasonryModule,
    ReactiveFormsModule,NgbModule,
    NgxPaginationModule,
    DataTablesModule,
    AngularMultiSelectModule,
    QuillModule.forRoot(),
    FormsModule,SharedModule,
    RouterModule.forChild(appsRoutes)
  ],
  schemas: [ CUSTOM_ELEMENTS_SCHEMA ],
  exports:[TicketsComponent,SidebarComponent,TicketListComponent,CommentComponent,InprogressComponent,ResolvedListComponent,ReopenedListComponent,EditTicketComponent]
})
export class TicketManagmentModule { }
