  constructor(private router: Router,private baseService: BaseServiceService,config: NgbModalConfig,public _route : ActivatedRoute, private modalService: NgbModal) { 
    this.user_type=JSON.parse( window.localStorage.getItem("userData")).user_type;
    this.userData  = JSON.parse( window.localStorage.getItem("userData"));
    this.loginType = window.localStorage.getItem("loginType");
    this._id  = JSON.parse( window.localStorage.getItem("userData"))._id;
    if(!!window.localStorage.getItem('index'))
    {
      this.obj.nextId = window.localStorage.getItem('index');
    }
    if(!!window.localStorage.getItem('PageNumber'))
    {
      this.p = Number(window.localStorage.getItem('PageNumber'));
    }
    if(!!window.localStorage.getItem('skip'))
    {
      this.skip = Number(window.localStorage.getItem('skip'));
    }
    if(this.userData.role != null && this.userData.role.isSuperAdmin)
    {
      this.type="Dept Tickets";
      this.isSuperAdmin=true;
      if(!!localStorage.getItem('superAdminSelection'))
      {
        this.superAdminData.deptName = localStorage.getItem('superAdminSelection');
      }
    }
    else if(this.user_type== 'Store')
    {
      this.type = "My Tickets"
    }
    if(JSON.parse( window.localStorage.getItem("userData")).user_type)
    {
     this.isSales =  JSON.parse( window.localStorage.getItem("userData")).user_type == "Sales" ? true : false;
    }
   this.getStatusNames();
   this.getMainDept(); 
   if(this.isSales)
    {
      this.getSalesStore();
    }
  }

  //////////Json To CSV Download 
  public downloadFile(data:any, filename? : string){
    let csvData = this.convertToCSV(data);
    let file = new Blob([csvData], { type: 'text/csv;charset=utf-8' });
    saveAs(file,"HappiTicketMgmt.csv");
}

public convertToCSV(objArray: any, fields?) {
  let json2csvParser = new this.Json2csvParser({ opts: fields });
  let csv = json2csvParser.parse(objArray);
  // console.log(csv);
  return csv;
}


