//Get Stores List
getSalesStore()
{
  axios.get(this.baseService.baseUrl + 'secure/users/getSalesStore/'+this._id,{
    headers: {
      'x-auth' : this.baseService.authToken
    },
  })
    .then(response => {        
      if (response.data.status) {
        console.log(response);
        this.salesStoresData = response.data.data.store_id;
        console.log("==============================");
        console.log(this.salesStoresData );
        if(!!window.localStorage.getItem('SalesPersonSelection'))
       {
        this.salesPersonData = JSON.parse(window.localStorage.getItem('SalesPersonSelection'));
        }
       else
        {
        this.salesPersonData.store = this.salesStoresData[0]._id;
        }
        this.callStoresTicketsData();
      }
      else if(this.baseService.checkToken(response.data.message , response.data.status))
      {
        
      }
      else{
        Swal.fire({
          position: 'center',
          icon: 'error',
          title: response.data["message"],
          showConfirmButton: false,
          timer: 3000
      })
       }
    })
    .catch(function (error) {
      console.log(error)
    })
}
