import { Component, OnInit } from '@angular/core';
import { BaseServiceService } from '@app/services/base-service.service';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import Swal from 'sweetalert2';
import { Subject } from 'rxjs';
import Axios from 'axios';

class DataTablesResponse {
  data: any[];
  draw: number;
  recordsFiltered: number;
  recordsTotal: number;
}


@Component({
  selector: 'app-comment',
  templateUrl: './comment.component.html',
  styleUrls: ['./comment.component.scss']
})
export class CommentComponent implements OnInit {
  dtOptions: DataTables.Settings = {};
  // We use this trigger because fetching the list of persons can be quite long,
  // thus we ensure the data is fetched before rendering
  dtTrigger: Subject<any> = new Subject<any>();
  commentListData: any;
  model: NgbDateStruct;
 newComment:any = {};
 commentStatus:boolean=false;
 fullPage: boolean = true;
 tagNames:any=[];
 statusNames:any=[];

 public config: any = {
   uiColor: 'transparent',
   // Define the toolbar groups as it is a more accessible solution.
   toolbarGroups: [
     {'name': 'basicstyles', 'groups': ['basicstyles']},
     // {'name': 'links', 'groups': ['links']},
     {'name': 'paragraph', 'groups': ['list', 'blocks']},
     // {'name': 'document', 'groups': ['mode']},
     // {'name': 'insert', 'groups': ['insert']},
     {'name': 'styles', 'groups': ['styles']},
     {'name': 'about', 'groups': ['about']}
   ],
   // Remove the redundant buttons from toolbar groups defined above.
   removeButtons: 'Underline,Strike,Subscript,Superscript,Anchor,Styles,Specialchar'
 };
 token_id: any;
  ticket_id: string;
  userId: any;
 
 constructor(private router: Router, private baseService:BaseServiceService, private _router:ActivatedRoute) { 
  this.ticket_id = this._router.snapshot.queryParamMap.get('ticket_id');
  console.log("---- basic Data ----");
  console.log(this.ticket_id);
  
  this.userId  = JSON.parse( window.localStorage.getItem("userData"))._id;
  // this.user_id = this._router.snapshot.queryParamMap.get('user_id');
  // this.ticket_id = this._router.snapshot.queryParamMap.get('ticket_id');
  this.newComment={user_id : this.userId,created_by : this.ticket_id};
  console.log("---- basic Data ----");
  console.log(this.newComment);
  this.getCommentList()

 }


 formSubmit()
 {
   console.log(this.newComment);
   Axios.post(this.baseService.baseUrl + 'secure/comment/addComment',this.newComment,{
    headers: {
      'x-auth' : this.baseService.authToken
    },
   })
     .then(response => {        
       if (response.data.status) {
         Swal.fire({
           position: 'center',
           icon: 'success',
           title: response.data["message"],
           showConfirmButton: false,
           timer: 3000
       }).then(() => {
         location.reload(); });
       } 
else if(this.baseService.checkToken(response.data.message , response.data.status))
{
  
}
       else{
        Swal.fire({
          position: 'center',
          icon: 'error',
          title: response.data["message"],
          showConfirmButton: false,
          timer: 3000
      })
       }
     })
     .catch(function (error) {
       console.log(error)
     })
 }

 resetForm(){
   this.newComment = {};
   return;
 }

 ngOnInit() {
  this.dtOptions = {
    pagingType: 'full_numbers',
    pageLength: 10
  };
 }

 getCommentList() {
  Axios.get(this.baseService.baseUrl + 'secure/comment/getComment', {

    headers: {
      'x-auth' : this.baseService.authToken
    },
  })
    .then(response => {
       console.log(response);
       if(response.data.status)
       {
       this.commentListData=response.data.data;
      this.dtTrigger.next();
       }     
else if(this.baseService.checkToken(response.data.message , response.data.status))
{
  
}
else
{
}
    })
    .catch(function (error) {
      console.log(error)
    })
}

commentsShow()
{
   this.commentStatus = !this.commentStatus;   
}

 ngOnDestroy() {
    // Do not forget to unsubscribe the event
    this.dtTrigger.unsubscribe();
  }


}
